@import '/src/vars.scss';

.news {
  &-wrap {
    background-color: #f2f2f2;
    padding: 6rem 0;
  }
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(288px, 1fr));
  grid-gap: 1rem;
  width: 100%;
  &__card {
    position: relative;
    background-color: $white;
    overflow: hidden;
    transition: all 333ms;
    &:hover {
      .news__content {
        top:0;
        opacity: 1;
      }
    }
  }
  &__date {
    position: absolute;
    top: 0;
    z-index: 3;
    left: 1.5rem;
    background-color: $accent;
    color: $white;
    padding: .5rem;
  }
  &__img {
    width: 100%;
  }
  &__content {
    padding: 3.5rem 1.5rem 1.5rem 1.5rem;
    position: absolute;
    z-index: 2;
    background-color: $white;
    height: 100%;
    pointer-events: none;
    opacity: 0;
    transition: all 300ms;
    .news__title {
      margin: 0;
    }
  }
  &__title {
    margin: 1.5rem;
  }
}