@import '../../vars.scss';

.main-footer {
  padding: 4rem;
  background-color: $black;
  @media (max-width: 576px) {
    padding: 0;
  }
  &__img-wrap {
    width: 100%;
    padding-bottom: 2rem;
    border-bottom: 1px solid rgba($color: $white, $alpha: .25);
    img {
      width: 100%;
    }
  }
  &__content {
    width: 100%;
    padding-top: 2rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    grid-gap: 2rem;
  }
  &__col, &__link-list {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__col {
    min-width: 220px;
    @media (max-width: 1500px) {
      margin-bottom: 2rem;
    }
    &:first-child {
      min-width: inherit;
    }
    p {
      max-width: 20rem;
    }
  }
  &__logo {
    max-width: 70px;
    margin-bottom: auto;
  }
  &__title {
    color: $white;
    margin-bottom: 1.5rem;
    text-transform: uppercase;
    font-size: 1rem;
  }
  &__link-item {
    margin-bottom: 0.5rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__link {
    color: rgba($color: $white, $alpha: .35);
    transition: all 300ms;
    &:hover {
      color: $accent;
    }
  }
  &__tel {
    margin-bottom: auto;
    font-size: 1.5rem;
    color: $white;
    transition: all 300ms;
    &:hover {
      color: $accent;
    }
    img {
      margin-right: 1rem;
    }
  }
}