@import '../../vars.scss';

.faq {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 1rem;
  &__title {
    width: 100%;
    background-color: $grey-second;
    padding: 2rem;
    text-align: left;
    border: none;
    font-size: 1.25rem;
    font-weight: 700;
    cursor: pointer;
    span {
      font-size: 1.75rem;
      color: rgba($color: $black, $alpha: .5);
      margin-right: 1rem;
    }
  }
  &__text {
    padding: 1rem;
    background-color: rgba($color: $grey-second, $alpha: .25);
    border: 2px solid $grey-second;
  }
}