@import '/src/vars.scss';

.cars {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(288px, 1fr));
  grid-gap: 1rem;
  width: 100%;
  .car {
    width: 100%;
    max-width: 370px;
    transition: all 300ms;
    @media (max-width: 1000px) {
      max-width: 450px;
    }
    &:hover {
      box-shadow: 0 0 12px rgba($color: $black, $alpha: .25);
    }
    &:last-child {
      margin-right: 0;
    }
    &__body {
      padding: 0 .5rem .5rem .5rem;
    }
    &__img {
      width: 100%;
    }
    &__title {
      font-size: 1rem;
      font-weight: 700;
      color: $black;
      margin-top: 0.5rem;
      margin-bottom: 0.25rem;
    }
    &__tech {
      font-size: 0.75rem;
      font-weight: 300;
      color: $grey;
    }
    &__info {
      padding: 1rem 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 0.75rem;
      &-item {
        display: flex;
        flex-direction: column;
        margin-right: 1rem;
        color: $grey;
        &:last-child {
          margin-right: 0;
        }
      }
      &-item-text {
        font-weight: 300;
        color: $grey;
        &_bold {
          color: $black;
          font-weight: 700;
        }
      }
    }
  }
}

.ticket-rate {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(288px, 1fr));
  grid-column-gap: 2rem;
  grid-row-gap: 1rem;
  &__card {
    border: 1px solid $accent;
    padding: 2rem;
    background-color: $white;
    color: $black;
    cursor: pointer;
    transition: all 300ms;
    &_active {
      background-color: $accent;
      color: $white;
      .ticket-rate__price {
        color: $black;
        background-color: $white;
      }
    }
    &:hover {
      box-shadow: 0 4px 12px rgba($color: $black, $alpha: .25);
    }
  }
  &__title-wrap {
    display: flex;
    align-items: center;
  }
  &__title {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2rem;
    margin-right: 1rem;
  }
  &__price {
    background-color: $accent;
    color: $white;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 700;
    padding: .3rem 1rem;
    border-radius: 1rem;
  }
}

.total-cars {
  width: 100%;
  &__title {
    width: 100%;
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(288px, 1fr));
    grid-gap: 1rem;
  }
  &__card {
    cursor: pointer;
  }
  &__img {
    width: 100%;
    box-shadow: 0 5px 12px rgba($color: $black, $alpha: .25);
    transition: all 300ms;
    &:hover {
      box-shadow: 0 5px 20px rgba($color: $black, $alpha: .45);
    }
  }
  &__link {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-size: .875rem;
    font-weight: 700;
    margin-top: 1rem;
    color: $black;
    img {
      margin-right: 1rem;
    }
  }
}

.winners {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(288px, 1fr));
  grid-gap: 1rem;
  width: 100%;
  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__img {
    width: 100%;
    max-width: 260px;
  }
  &__icon {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }
}

.home-banner__img {
  width: 100%;
  box-shadow: 0 0 12px rgba($color: $black, $alpha: .25);
}

.check-ticket {
  background-image: url(../../assets/images/check-ticket-bg.jpg);
  padding: 6rem 0;
  color: $white;
  text-transform: uppercase;
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title {
    font-size: 3.5rem;
    line-height: 4rem;
  }
  &__form {
    display: flex;
    align-items: center;
  }
  &__label {
    font-size: 1rem;
  }
  &__input-wrap {
    margin-right: 1rem;
  }
  &__input {
    font-size: 1.5rem;
    font-weight: 700;
    padding: .5rem 1rem;
    width: 230px;
    height: 75px;
    background-color: $white;
    border: 2px solid transparent;
    border-radius: 3rem;
    margin-top: 1rem;
    transition: all 300ms;
    &::-webkit-inner-spin-button, -webkit-outer-spin-button {
      display: none;
    }
    &:focus {
      outline: none;
      border-color: $accent;
    }
  }
  &__button {
    font-size: 1rem;
    font-weight: 700;
    width: 100%;
    max-width: 230px;
    height: 75px;
    border: none;
    border-radius: 3rem;
    margin-top: 35px;
    cursor: pointer;
  }
}

.home-banner-add-car {
  background-image: url(../../assets/images/home-new-auto-bg-2x.jpg);
  padding: 6rem 0;
  &__content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: $white;
  }
  &__title {
    max-width: 50rem;
    font-size: 3.5rem;
    font-weight: 700;
    text-align: center;
    span {
      color: $accent;
    }
  }
  &__subtitle {
    font-size: 1rem;
    margin: 2rem 0;
  }
  &__button {
    padding: 1.5rem 2rem;
    border-radius: 2rem;
    box-shadow: 0 4px 12px rgba($color: $black, $alpha: .25);
    &:hover {
      box-shadow: 0 4px 20px rgba($color: $black, $alpha: .5);
    }
  }
}

.home-banner-3 {
  background-image: url(../../assets/images/home-banner-3.jpg);
  padding: 6rem 0;
  color: $white;
  &__content {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__col {
    display: flex;
    flex-direction: column;
  }
  &__title {
    font-size: 3.5rem;
  }
  &__link {
    margin-top: 1rem;
    display: flex;
    align-items: center;
    font-size: 1rem;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    transition: all 333ms;
    &:hover {
      color: $accent;
      text-decoration: underline;
    }
    img {
      margin-right: 0.5rem;
    }
  }
  &__button {
    margin: auto 0;
    padding: 1.5rem 3rem;
    border-radius: 2rem;
    box-shadow: 0 0 12px rgba($color: $black, $alpha: .25);
    &:hover {
      box-shadow: 0 0 20px rgba($color: $black, $alpha: .5);
    }
  }
}

.home-game-info {
  &__content, &__img, &__img img, &__body {
    width: 100%;
  }
  &__body {
    padding: 0 2rem;
  }
  &__img img {
    max-width: 700px;
  }
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $dark-bg;
    color: $white;
  }
  &__title {
    font-size: 3rem;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 3rem;
  }
  &__info-list {
    width: 100%;
    display: flex;
    align-items: center;
  }
  &__info-item {
    margin-right: 2rem;
    &:last-child {
      margin-right: 0;
    }
    span {
      color: $accent;
      font-size: 1rem;
    }
    p {
      font-size: 1.5rem;
      font-weight: 700;
    }
  }
}