@import '/src/vars.scss';

.main-header {
  color: $white;
  background-color: $dark-bg;
  padding: 1rem 0;
  font-size: 1rem;
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1;
  }
  &__text {
    text-transform: uppercase;
    font-weight: 500;
  }
  &__buttons, &__langs {
    display: flex;
    align-items: center;
  }
  &__lang {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    text-transform: uppercase;
    margin-right: 1rem;
    cursor: pointer;
    &_active {
      background-color: $gray-bg;
      border-radius: 50%;
    }
  }
  &__button  {
    margin-right: 1rem;
    border-radius: 2rem;
    border: 1px solid $accent;
    background-color: $accent;
    padding: 1rem;
    &:last-child {
      margin-right: 0;
    }
    &_outline {
      background-color: $dark-bg;
    }
  }
}