@import 'vars';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&display=swap');

* {
    box-sizing: border-box;
  }
  
body, html {
  width: 100%;
  height: 100%;
  font-size: 16px;
}

#root {
  display: flex;
  height: 100%;
}

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.42;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  background-color: $white;
  overflow-x: hidden;
}

ul, p, label {
  padding: 0;
  margin: 0;
  li {
    list-style: none;
  }
}

a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: 700;
}

.wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  position: relative;
  width: 100%;
}

.container {
  flex: 1 1 auto;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.main-content {
  flex-grow: 1;
}

.hide {
  position: absolute;
  left: -9999px;
  top: -9999px;
}
section {
  padding: 2rem 0;
}

.section-title {
  width: 100%;
  font-size: 3rem;
  font-weight: 700;
  color: $black;
  margin-bottom: 2rem;
  text-transform: uppercase;
}

.burger-menu {
  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: gray-bg;
    cursor: pointer;
    &_line {
      width: 26px;
      height: 3px;
      background-color: $white;
      margin-bottom: 3px;
    }
    &_line:last-child {
      margin-bottom: 0;
    }
  }
}

.main-button {
  color: $white;
  background-color: $accent;
  text-transform: uppercase;
  font-size: .875rem;
  transition: all 333ms;
}

.background-centered {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}